.page-body {
  margin: 0;
  padding: 0;
  font-family: "Open Sans", "Arial", sans-serif;
  font-size: 14px;
  font-weight: normal;
  line-height: 21px;
  color: #000000;
}
.wrapper {
  width: auto;
  margin: 0 30px;
}
@media (min-width: 768px) {
  .wrapper {
    width: auto;
    margin: 0 50px;
  }
}
@media (min-width: 1280px) {
  .wrapper {
    width: 1180px;
    margin: 0 auto;
  }
}
.main-content {
  padding: 20px 0;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
@font-face {
  font-family: "Open Sans";
  font-weight: normal;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/OpenSans-Regular.woff2") format("woff2"), url("../fonts/OpenSans-Regular.woff") format("woff");
}
@font-face {
  font-family: "Open Sans";
  font-weight: bold;
  font-style: bold;
  font-display: swap;
  src: url("../fonts/OpenSans-Bold.woff2") format("woff2"), url("../fonts/OpenSans-Bold.woff") format("woff");
}
.page-header {
  width: 100%;
  min-height: 64px;
  padding: 15px 0;
  border-bottom: 1px solid rgba(0, 0, 0, 0.3);
}
.page-header-wrapper {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  gap: 30px;
}
.page-nav__list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
}
@media (min-width: 768px) {
  .page-nav__list {
    gap: 20px;
  }
}
.page-nav__link {
  font-size: 18px;
  line-height: 32px;
  font-weight: normal;
  text-decoration: none;
  color: #000000;
}
.page-nav__link:hover {
  text-decoration: underline;
}
.page-nav__link:active {
  opacity: 0.7;
}
.about-me {
  padding: 15px 0;
}
@media (min-width: 768px) {
  .about-me {
    padding: 25px 0;
  }
}
.about-me-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
}
@media (min-width: 768px) {
  .about-me-wrapper {
    flex-direction: row;
  }
}
@media (min-width: 1280px) {
  .about-me-wrapper {
    gap: 75px;
  }
}
.about-me__pic {
  max-width: 200px;
}
.about-me__img {
  border-radius: 50%;
  width: 100%;
  height: auto;
}
.about-me__desk {
  text-align: center;
}
.about-me__text {
  margin: 0;
}
.skills {
  padding: 15px 0;
}
@media (min-width: 768px) {
  .skills {
    padding: 25px 0;
  }
}
.skills__header {
  font-size: 24px;
  line-height: 32px;
  font-weight: 700;
  color: #000000;
  text-align: center;
  margin: 0;
  padding: 0;
  margin-bottom: 35px;
}
@media (min-width: 768px) {
  .skills__header {
    font-size: 36px;
    line-height: 55px;
    font-weight: 700;
  }
}
.skills__list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
}
@media (min-width: 768px) {
  .skills__list {
    justify-content: space-between;
  }
}
.skills__item {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 15px;
}
.skills__icon {
  width: 75px;
  height: 75px;
}
.skills__name {
  margin: 0;
}
.portfolio {
  padding: 15px 0 50px;
}
@media (min-width: 768px) {
  .portfolio {
    padding: 25px 0 50px;
  }
}
.portfolio__header {
  font-size: 24px;
  line-height: 32px;
  font-weight: 700;
  color: #000000;
  text-align: center;
  margin: 0;
  padding: 0;
  margin-bottom: 35px;
}
@media (min-width: 768px) {
  .portfolio__header {
    font-size: 36px;
    line-height: 55px;
    font-weight: 700;
  }
}
.portfolio__list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  justify-content: center;
  align-content: center;
  gap: 30px;
}
.portfolio__item {
  min-width: 260px;
  max-width: 320px;
  width: 100%;
  height: auto;
  border-radius: 13px;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.15), 0 3px 6px rgba(0, 0, 0, 0.25);
  overflow: hidden;
  box-sizing: border-box;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
}
@media (min-width: 768px) {
  .portfolio__item {
    max-width: 100%;
    width: 100%;
    flex-wrap: nowrap;
    flex-direction: row;
    gap: 1%;
  }
}
.portfolio__img {
  width: auto;
  height: 226px;
  background-color: #000000;
}
@media (min-width: 768px) {
  .portfolio__img {
    margin: auto 0;
  }
}
.portfolio__info {
  height: auto;
  padding: 20px;
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  gap: 20px;
}
.portfolio__info-container {
  display: flex;
  flex-direction: column;
  gap: 20px;
}
.portfolio__name {
  font-size: 20px;
  line-height: 26px;
  font-weight: 700;
  margin: 0;
}
@media (min-width: 768px) {
  .portfolio__name {
    font-size: 24px;
    line-height: 32px;
    font-weight: 700;
  }
}
.portfolio__date {
  font-size: 14px;
  line-height: 21px;
  font-weight: 400;
  margin: 0;
}
@media (min-width: 768px) {
  .portfolio__date {
    font-size: 16px;
    line-height: 24px;
    font-weight: 400;
  }
}
.portfolio__desc {
  font-size: 14px;
  line-height: 21px;
  font-weight: 400;
  margin: 0;
}
@media (min-width: 768px) {
  .portfolio__desc {
    font-size: 16px;
    line-height: 24px;
    font-weight: 400;
  }
}
.portfolio__skills {
  font-size: 14px;
  line-height: 21px;
  font-weight: 400;
  margin: 0;
}
@media (min-width: 768px) {
  .portfolio__skills {
    font-size: 16px;
    line-height: 24px;
    font-weight: 400;
  }
}
.portfolio__link-container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  gap: 20px;
  margin-top: auto;
}
.portfolio__link {
  background-color: #8250df;
  color: #FFFFFF;
  font-size: 14px;
  line-height: 24px;
  font-weight: 700;
  text-decoration: none;
  padding: 10px 15px;
  border-radius: 8px;
  min-width: 100px;
  text-align: center;
}
@media (min-width: 768px) {
  .portfolio__link {
    font-size: 16px;
    line-height: 24px;
    font-weight: 700;
  }
}
.portfolio__link:hover {
  background-color: #8250df;
  box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25);
}
.portfolio__link:active,
.portfolio__link:focus {
  color: rgba(255, 255, 255, 0.75);
  background-color: #501faa;
  box-shadow: none;
}
.page-footer {
  padding: 15px 0 25px;
  border-top: 1px solid rgba(0, 0, 0, 0.3);
}
@media (min-width: 768px) {
  .page-footer {
    padding: 25px 0 35px;
  }
}
.footer-contacts__list {
  list-style: none;
  padding: 0;
  margin: 0;
  height: min-content;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: 50px;
}
.footer-contacts__item {
  width: 50px;
  height: 50px;
}
.footer-contacts__link {
  padding: 0;
  margin: 0;
}
.footer-contacts__icon {
  width: auto;
  height: 100%;
}
